@import url(https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,600;1,300&display=swap);
html{font-size:62.5%}body{touch-action:manipulation;background:#222;font-family:'Montserrat', Helvetica, Arial, sans-serif;font-size:1.6rem}button{cursor:pointer}button:disabled{cursor:default}input{border:none;background-image:none;background-color:transparent;box-shadow:none;box-sizing:content-box}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type='number']{-moz-appearance:textfield}.add-option-error{color:#cad4cc;font-style:italic;margin:1.6rem 0 0 0;padding:0 1.6rem}.add-option{display:flex;flex-direction:column;padding:1.6rem}.add-option__input{background:#222;border:none;border-bottom:0.3rem solid #090909;flex-grow:1;margin:0 0 1.2rem 0;color:#cad4cc;padding:1.2rem}@media (min-width: 45rem){.add-option{flex-direction:row}.add-option__input{margin:0 1.2rem 0 0}}.big-button{background:#238636;border:none;border-bottom:0.6rem solid #185e26;color:white;font-weight:bold;font-size:3.2rem;width:100%;margin-bottom:1.6rem;padding:2.4rem}.big-button:disabled{background:#e60023;border-bottom:0.6rem solid #b3001b}.button{background:#238636;border:none;border-bottom:0.3rem solid #185e26;color:white;font-weight:500;padding:1.2rem;min-width:calc(4.8rem + 2px)}.button--link{background:none;border:none;color:#cad4cc;padding:0}@media (min-width: 45rem){.big-button{margin-bottom:4.8rem}}.end-round{margin-top:1.2rem;margin-bottom:0}.container{max-width:60rem;margin:0 auto;padding:0 1.6rem}.header{background:#111;color:white;margin-bottom:1.6rem;padding:1.6rem 0}.header__title{font-size:3.2rem;margin:0}.header__subtitle{color:#cad4cc;font-size:1.6rem;font-weight:500;margin:0}@media (min-width: 45rem){.header{margin-bottom:4.8rem}}.ReactModalPortal>div{opacity:0}.ReactModalPortal .ReactModal__Overlay{align-items:center;display:flex;justify-content:center;transition:opacity 200ms ease-in-out}.ReactModalPortal .ReactModal__Overlay--after-open{opacity:1}.ReactModalPortal .ReactModal__Overlay--before-close{opacity:0}.modal{background:#333;color:white;min-width:30rem;outline:none;padding:3.2rem;text-align:center}.modal__title{margin:0 0 1.6rem 0}.modal__body{font-size:2rem;font-weight:300;margin:0 0 3.2rem 0;word-break:break-all}.modal-child{height:4.8rem;align-items:center}.option{border-bottom:1px solid #e6ebe7;display:flex;justify-content:space-between;padding:1.6rem 1.6rem;font-size:large}.option__text{color:white;max-width:80%;font-weight:500;font-size:2rem;margin:auto 0;word-break:break-all}.option__action{display:flex}.option__action input{line-height:0;font-family:'Zen Dots', cursive;text-align:center;display:flex;color:white;background:#222;width:calc(3.2rem + 5px);height:calc(3.2rem + 5px);justify-content:center;align-items:center;border-bottom:0.3rem solid #4d4d4d}.option__action button{margin-left:1.2rem;border-radius:6%;line-height:0;height:4.8rem;font-size:xx-large}.minus{background:#e60023;border-bottom:0.3rem solid #b3001b}@media (min-width: 45rem){.option__action input{width:calc(4.8rem - 5px);height:calc(4.8rem - 5px)}}.widget{background:#333;margin-bottom:4.8rem}.widget-header{background:#272727;color:#cad4cc;display:flex;justify-content:space-between;padding:1.6rem}.widget-header__title{margin:0}.widget-message{color:#cad4cc;margin:0;padding:3.2rem;text-align:center;border:none;border-bottom:1px solid #e6ebe7}

